<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="selectAtomCourse">
        <ToolBar>
            <div></div>
            <div>
                <SelectTags v-model="labelList" />
                <el-input
                        placeholder="请输入原子课名称或原子课ID"
                        size="small"
                        style="width: 220px"
                        v-model="searchParams.keyWord"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.sellType" size="small" clearable placeholder="售卖类型"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.sellTypes"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.limtFree" size="small" clearable placeholder="限时免费"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.freeLimitedTime"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select
                        v-model="searchParams.discountType"
