                    <el-option v-for="(v,k) in $Cfg.discountTypes" :key="k" :label="v" :value="k"></el-option>
                </el-select>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small">添加</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="原子课名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="运营标签">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="售卖类型"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="limitedTime"
                    label="限时免费"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="sellMoney"
                    label="售卖金额"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="discount"
                    label="折扣类型"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="time"
                    label="发布时间"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="status"
                    label="状态"
                    width="80">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="78">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="info" size="small"
                    >预览
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
                :params="searchParams"
                :requestFunc="requestFunc"
                @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                labelList: [],
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {
                    }
                });
            },

        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination, SelectTags
        }
    }
</script>
